/* PAGE BASE */
body {
  margin: 0;
  min-height: 100vh;
  font-family: system-ui, -apple-system, sans-serif;
  background-color: #2f4f4f;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* CARD */
.card {
  width: 420px;
  background-color: #f3ffff;
  padding: 32px 28px;
  border-radius: 14px;
  box-shadow: 0 14px 35px rgba(0, 0, 0, 0.25);
  position: relative;
}

/* TITLE */
.card h1 {
  margin: 0 0 18px 0;
  text-align: center;
  font-size: 22px;
  font-weight: 600;
}

/* PASSWORD OUTPUT */
.card h2 {
  margin: 0 0 22px 0;
  padding: 14px;
  background-color: #e6f4f1;
  border-radius: 8px;
  font-family: monospace;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  word-break: break-all;
}

/* RANGE SLIDER */
#rangeInput {
  width: 100%;
  margin-bottom: 6px;
  cursor: pointer;
}

/* LENGTH LABEL */
.card > label {
  display: block;
  margin-bottom: 18px;
  font-size: 14px;
}

/* CHECKBOXES */
#numberCheckbox,
#charCheckbox {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  cursor: pointer;
}

/* CHECKBOX LABELS */
#numberLabel,
#charLabel {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  cursor: pointer;
}

/* FORCE SECOND CHECKBOX TO NEW LINE */
#charCheckbox {
  margin-top: 14px;
}

/* SPACE BETWEEN CHECKBOX ROWS */
#numberLabel {
  margin-bottom: 14px;
  display: inline-block;
  
 
}

/* OPTIONAL: improve checkbox click area */
#numberLabel,
#charLabel {
  line-height: 1.4;
}

#charLabel{
  margin-left: 26px;
}
#numberCheckbox{
  position: relative;
  bottom: 6px;
}
#charCheckbox{
  position: absolute;
bottom: 49px;
left: 28px;
}